/// variables ///
$base-color: #57616b;
$hover-color: #8564c1;
$white-color: #ffffff;
$estimator_color: #bbc1cd;
$tables_background: #fafcfd;
/// /variables ///

/// mixins ///
@mixin font-grant {
    font-family: 'Grand Hotel', cursive; }

@mixin font-open {
    font-family: 'Open Sans', sans-serif; }

@mixin font-work {
    font-family: 'Work Sans', sans-serif; }

@mixin flex {
    display: flex;
    align-items: center;
    justify-content: space-between; }
@mixin button {
    border: none;
    outline: none;
    color: $estimator_color;
    background-color: $white-color;
    min-width: 230px;
    padding: 20px 25px;
    border-radius: 30px;
    text-transform: uppercase;
    border: 2px solid $estimator_color;
    font-weight: 500;
    cursor: pointer;
    transition: .3s linear;
    &:hover {
        color: $white-color;
        background-color: $hover-color;
        border: 2px solid $hover-color; }
    @media (max-width: 1024px) {
        min-width: 200px; } }
/// /mixins ///

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
body {
    color: $base-color;
    &::-webkit-scrollbar {
        width: 16px; }
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(133, 100, 193, 1); }
    &::-webkit-scrollbar-thumb {
        border-radius: 15px;
        background-color: $hover-color;
        outline: 1px solid $hover-color; } }
.section_outer {
    max-width: 1140px;
    margin: 0 auto; }
.section_header {
    width: 100%;
    box-shadow: 0px 0px 7px 0px #ccc;
    height: 94px;
    .mobile_menu_wrapper {
        display: none; }
    &_content {
        @include flex;
        height: 100%; }
    &_logo {
        @include font-grant;
        font-size: 52px;
        color: $hover-color;
        position: relative;
        width: 250px;
        a {
            text-decoration: none;
            color: $hover-color; }
        span {
            position: absolute;
            left: 15%;
            bottom: 0;
            @include font-open;
            font-size: 12px;
            font-weight: 800; } }
    &_nav_list {
        list-style: none;
        @include flex;
        a {
            text-decoration: none;
            text-transform: uppercase;
            color: $base-color;
            @include font-work;
            font-size: 15px;
            font-weight: 500;
            padding: 36px 0px;
            margin: 0 20px;
            border-bottom: 2px solid $white-color;
            transition: .2s linear;
            &:hover {
                border-bottom: 2px solid $hover-color;
                color: $hover-color;
                transition: .2s linear; } } }
    &_btn_book_now {
        a {
            background-color: $hover-color;
            @include font-work;
            font-size: 15px;
            font-weight: 500;
            text-transform: uppercase;
            color: $white-color;
            padding: 38px 30px;
            text-decoration: none;
            &:hover {} } } }
.section_heading {
    text-transform: capitalize;
    font-size: 42px;
    font-weight: 500;
    text-align: center; }
.section_paragraph {
    line-height: 26px;
    font-size: 18px;
    font-weight: 500;
    color: $estimator_color;
    max-width: 60%;
    margin: 0 auto;
    text-align: center; }
.fixed_phone {
    position: fixed;
    right: 0;
    top: calc(50% - 25px);
    z-index: 5;
    a {
        @include font-work;
        padding: 15px 20px 15px 30px;
        text-decoration: none;
        color: $base-color;
        background-color: $white-color;
        box-shadow: 0px 6px 10px 3px #ccc;
        border-radius: 5px;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            left: 7px;
            bottom: 13px;
            width: 17px;
            height: 20px;
            background-image: url(../img/phone_icon.png);
            background-size: contain;
            background-repeat: no-repeat; } } }
.section_services {
    margin-top: 130px;
    text-align: center;
    @include font-work;
    &_price_block {
        max-width: 85%;
        margin: 0 auto; }
    &_price_list {
        list-style: none;
        @include flex;
        margin-top: 80px; }
    &_price_item {
        width: 170px;
        height: 170px;
        border-radius: 5px;
        background-color: $hover-color;
        display: flex;
        flex-direction: column;
        color: #4b3770;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: -20px;
            left: calc(50% - 20px);
            width: 40px;
            height: 20px;
            border-radius: 50% 50% 00 / 100% 100% 0 0;
            background-color: $hover-color; }
        &::after {
            content: '';
            position: absolute;
            bottom: -20px;
            left: calc(50% - 20px);
            width: 40px;
            height: 20px;
            border-radius: 0 0 50% 50% / 0 0 100% 100%;
            background-color: $hover-color; }
        &_service {
            height: 25%;
            width: 80%;
            display: flex;
            align-items: baseline;
            justify-content: center;
            font-size: 18px;
            font-weight: 600;
            text-transform: capitalize;
            margin: 15% auto 0; }
        &_rate {
            color: $white-color;
            font-size: 41px;
            padding: 5px; }
        &_time {
            text-transform: uppercase;
            font-weight: 500; } } }
.section_estimator {
