    &_options_block {
        width: 660px;
        margin-right: 80px;
        .estimator_small_option {
            width: 70px;
            height: 70px;
            border: 2px solid $estimator_color;
            border-radius: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 20px;
            font-weight: 500;
            color: $estimator_color; }
        .estimator_big_option {
            width: 160px;
