            cursor: pointer;
            color: $white-color; }
        .estimator_label {
            width: 120px;
            font-size: 20px;
            color: $base-color;
            text-transform: capitalize;
            font-weight: 500; }
        .estimator_category:not(:last-child) {
            margin-bottom: 25px; }
        &_bedrooms {
            height: 70px;
            @include flex;
            div {
                transition: .3s linear;
                &:hover {
                    box-shadow: 0px 4px 9px 3px #ccc;
                    cursor: pointer; } } }
        &_bathrooms {
            height: 70px;
            @include flex;
            div {
                transition: .3s linear;
                &:hover {
                    box-shadow: 0px 4px 9px 3px #ccc;
                    cursor: pointer; } } }
        &_condition {
            height: 70px;
            @include flex;
            div {
                transition: .3s linear;
                &:hover {
                    box-shadow: 0px 4px 9px 3px #ccc;
                    cursor: pointer; } } }
        &_type {
            height: 70px;
