                    cursor: pointer; } } } }
    &_result_block {
        &_text {
            font-size: 20px;
            font-weight: 500;
            text-transform: capitalize;
            margin-bottom: 25px; }
        &_time {
            font-size: 20px;
            font-weight: 500;
            color: $hover-color;
            margin-bottom: 10px; }
        &_price {
            font-size: 65px;
            font-weight: 400;
            color: $hover-color;
            margin-bottom: 65px;
            display: inline-block;
            &_wrapper {
                position: relative; }
            &_cleaen {
                position: relative;
                display: inline-block;
                bottom: 18px;
                width: 50px;
                font-size: 14px;
                font-weight: 500;
                color: $estimator_color;
                text-transform: uppercase; } }
        &_btn {
            @include button; } } }
.section_slider {
    @include font-work;
    margin-top: 130px;
    margin-bottom: 200px;
    &_content {
        margin-top: 80px;
        &_wrapper:focus {
            outline: none; }
        &_slide {
            height: 300px;
            width: 360px;
            border: 10px solid #eef6f8;
            border-radius: 10px;
            position: relative;
            padding: 35px;
            transition: .3s;
            transform: scale(0) rotate(45deg);
            &::before {
                content: '';
                position: absolute;
                width: 0;
                height: 0;
                right: 0;
                top: 0;
                border-top: 60px solid #eef6f8;
                border-left: 60px solid transparent;
                z-index: -1; }
            &::after {
                content: '';
                position: absolute;
                width: 25px;
                height: 25px;
                right: 0;
                top: 0;
                background-color: $white-color;
                border-radius: 30px; }
            &_header {
                position: relative;
                margin-bottom: 30px; }
            &_heading {
                font-size: 31px;
                font-weight: 500;
                color: $hover-color;
                &_discount {
                    position: absolute;
                    top: 0;
                    left: 30%;
                    color: #ed5792;
                    .slide_heading_discount_save {
                        font-size: 17px;
                        font-weight: 500;
                        display: block; }

                    .slide_heading_discount_was {
                        font-size: 13px;
                        font-weight: 500;
                        display: block; } } }
            &_subheading {
                font-size: 20px;
                font-weight: 500; }
            &_main {
                margin-bottom: 40px; }
            &_content {
                font-size: 16px;
                color: $estimator_color;
                line-height: 24px; }
            &_footer_code {
                color: $hover-color; } } } }
.slick-dots {
    li.slick-active {
        button:before {
            color: #57616b;
            opacity: 1; } } }
.slick-dots {
    li {
        button:before {
            font-size: 20px;
            line-height: 20px;
            top: 70px;
            width: 20px;
            height: 20px;
            content: 'â€¢';
            -webkit-text-stroke: 1px #e1e9f1;
            opacity: 1;
            color: #fff; } } }
.slick-active {
    .section_slider_content_slide {
        transform: scale(1) rotate(0deg); } }
.section_tables {
